<template>
    <el-row class="picture-wrapper main-content">
        <el-col class="picture-container mini-scroll">
            <div class="picture-column-wrapper">
                <div class="picture-column">
                    <div class="picture-item-container wait-observer height-max"
                         v-for="item in pictureColumn0"
                         :key="item.id"
                         :data-name="item.name"
                         :data-show-src="item.show_url">
                        <el-image class="picture-item"
                                  fit="scale-down"
                                  :src="item.show_img">
                            <div slot="placeholder"
                                 class="image-placeholder">
                                <i class="el-icon-picture-outline"></i>
                            </div>
                            <div slot="error"
                                 class="image-error">
                                <i class="el-icon-picture-outline"></i>
                            </div>
                        </el-image>
                    </div>
                </div>
                <div class="picture-column">
                    <div class="picture-item-container wait-observer height-max"
                         v-for="item in pictureColumn1"
                         :key="item.id"
                         :data-name="item.name"
                         :data-show-src="item.show_url">
                        <el-image class="picture-item"
                                  fit="scale-down"
                                  :src="item.show_img">
                            <div slot="placeholder"
                                 class="image-placeholder">
                                <i class="el-icon-picture-outline"></i>
                            </div>
                            <div slot="error"
                                 class="image-error">
                                <i class="el-icon-picture-outline"></i>
                            </div>
                        </el-image>
                    </div>
                </div>
                <div class="picture-column">
                    <div class="picture-item-container wait-observer height-max"
                         v-for="item in pictureColumn2"
                         :key="item.id"
                         :data-name="item.name"
                         :data-show-src="item.show_url">
                        <el-image class="picture-item"
                                  fit="scale-down"
                                  :src="item.show_img">
                            <div slot="placeholder"
                                 class="image-placeholder">
                                <i class="el-icon-picture-outline"></i>
                            </div>
                            <div slot="error"
                                 class="image-error">
                                <i class="el-icon-picture-outline"></i>
                            </div>
                        </el-image>
                    </div>
                </div>
                <div class="picture-column">
                    <div class="picture-item-container wait-observer height-max"
                         v-for="item in pictureColumn3"
                         :key="item.id"
                         :data-name="item.name"
                         :data-show-src="item.show_url">
                        <el-image class="picture-item"
                                  fit="scale-down"
                                  :src="item.show_img">
                            <div slot="placeholder"
                                 class="image-placeholder">
                                <i class="el-icon-picture-outline"></i>
                            </div>
                            <div slot="error"
                                 class="image-error">
                                <i class="el-icon-picture-outline"></i>
                            </div>
                        </el-image>
                    </div>
                </div>
                <div class="picture-column">
                    <div class="picture-item-container wait-observer height-max"
                         v-for="item in pictureColumn4"
                         :key="item.id"
                         :data-name="item.name"
                         :data-show-src="item.show_url">
                        <el-image class="picture-item"
                                  fit="scale-down"
                                  :src="item.show_img">
                            <div slot="placeholder"
                                 class="image-placeholder">
                                <i class="el-icon-picture-outline"></i>
                            </div>
                            <div slot="error"
                                 class="image-error">
                                <i class="el-icon-picture-outline"></i>
                            </div>
                        </el-image>
                    </div>
                </div>
            </div>
            <div class="picture-load-more"></div>
        </el-col>
    </el-row>
</template>

<script>
export default {
    name: 'AppPicture',
    data: function () {
        return {
            pictureApi: 'https://api.github.com/repos/onedx1943/Picture/contents',
            pictureList: [],
            defaultImg: 'data:image/jpeg;base64,',
            observer: null,
            observedList: [],
        }
    },

    watch: {
        pictureList: function (val) {
            this.addToObserve()
        }
    },

    computed: {
        pictureColumn0: function () {
            return this.pictureList.filter((item, index) => index % 5 === 0);
        },
        pictureColumn1: function () {
            return this.pictureList.filter((item, index) => index % 5 === 1);
        },
        pictureColumn2: function () {
            return this.pictureList.filter((item, index) => index % 5 === 2);
        },
        pictureColumn3: function () {
            return this.pictureList.filter((item, index) => index % 5 === 3);
        },
        pictureColumn4: function () {
            return this.pictureList.filter((item, index) => index % 5 === 4);
        },
    },

    created: function () {
        this.loadData(this.pictureApi);
    },

    mounted: function () {
        let _this = this;
        _this.observer = new IntersectionObserver(function (changes) {
            changes.forEach(function (change) {
                if (change.isIntersecting) {
                    let container = change.target;
                    let name = container.getAttribute('data-name');
                    let url = container.getAttribute('data-show-src');
                    _this.getImage(name, 'show_url', url, container);
                    _this.observer.unobserve(container);
                }
            });
        }, {
            threshold: 0,
        });

        _this.addToObserve();

        // let loadMore = new IntersectionObserver(function (changes) {
        //     changes.forEach(function (change) {
        //         if (change.isIntersecting) {
        //             _this.showLength += 2;
        //         }
        //     });
        // }, {
        //     threshold: 0.4,
        // });
        // loadMore.observe(document.querySelector('.picture-load-more'));

        // ScrollReveal().reveal('.demo__bar:first-child .demo__bar__fill', {
        //     delay: 0,
        //     interval: 80
        // })
        // ScrollReveal().reveal('.picture-column > div', {
        //     delay: 500,
        //     useDelay: 'onload',
        //     reset: true,
        // });
    },

    methods: {
        loadData: function (url) {
            let _this = this;
            this.$http.get(url, {
                headers: {
                    'Authorization': _this.$token1 + _this.$token2 + _this.$token3
                }
            }).then(function (response) {
                _this.limitNotification(response.headers);
                let data = [];
                for (let i = 0; i < response.data.length; i++) {
                    if (response.data[i].type === 'file' && (response.data[i].name.endsWith('.gif') || response.data[i].name.endsWith('.png'))) {
                        if (response.data[i].name.endsWith('.gif')) {
                            _this.pictureList.push({
                                show_url: response.data[i].download_url.substring(0, response.data[i].download_url.lastIndexOf(".")) + '.jpg',
                                id: response.data[i].sha,
                                name: response.data[i].name,
                                preview_url: response.data[i].download_url.substring(0, response.data[i].download_url.lastIndexOf(".")) + '.mp4',
                                download_url: response.data[i].download_url,
                                show_img: 'data:image/jpeg;base64,',
                                is_gif: true
                            });
                        } else {
                            _this.pictureList.push({
                                show_url: response.data[i].download_url,
                                id: response.data[i].sha,
                                name: response.data[i].name,
                                preview_url: response.data[i].download_url,
                                download_url: response.data[i].download_url,
                                show_img: 'data:image/jpeg;base64,',
                                is_gif: false
                            });
                        }

                    } else if (response.data[i].type === 'dir') {
                        _this.loadData(response.data[i].url);
                    }
                }
            }).catch(function (error) {
                _this.$notify({
                    type: 'error',
                    title: '错误',
                    message: 'API访问失败，请检查',
                    offset: 60
                });
                console.log(error);
            });
        },

        limitNotification: function (headers) {
            let limit = headers['x-ratelimit-limit'];
            let remaining = headers['x-ratelimit-remaining'];
            if (parseInt(remaining) / parseInt(limit) < 0.4) {
                this.$notify({
                    type: 'warning',
                    title: '警告',
                    message: '请求速率限制: ' + remaining + '/' + limit + '每小时',
                    offset: 60
                })
            }
        },
        addToObserve: function () {
            let _this = this;
            _this.$nextTick(() => {
                document.querySelectorAll('.picture-item-container.wait-observer').forEach(function (item) {
                    item.classList.remove('wait-observer');
                    _this.observer.observe(item);
                });
            });
        },
        getImage: function (name, type, url, container) {
            let _this = this;
            let save_name = 'picture_' + name;
            this.$localforage.getItem(save_name).then(function (img_info) {
                if (img_info && img_info[type]) {
                    _this.changeImage(name, img_info[type], container);
                } else {
                    let ext = url.split('.').pop().toLowerCase();
                    _this.getBase64ImageUrl(url, ext, function (base64) {
                        let img = {};
                        img[type] = base64;
                        _this.$localforage.setItem(save_name, img).then(function () {
                            _this.changeImage(name, base64, container);
                            _this.$parent.refreshUsage();
                        });
                    });
                }
            }).catch(function (err) {
                console.log(err);
            });
        },
        changeImage: function (name, base64, container) {
            let index = this.pictureList.findIndex(item => item.name === name);
            if (index > 0) {
                this.pictureList[index]['show_img'] = base64;
            }
            container.classList.remove('height-max');
        },
        getBase64ImageUrl: function (url, ext, callback) {
            let xhr = new XMLHttpRequest();
            xhr.responseType = 'arraybuffer';
            xhr.open('GET', url, true);
            xhr.onload = function () {
                let result = xhr.response;
                let file = new File([result], 'image.' + ext, {
                    type: 'image/' + ext,
                });
                let reader = new FileReader();
                reader.onload = function (evt) {
                    callback(evt.target.result);
                };
                reader.readAsDataURL(file)
            };
            xhr.send(null);
        },
    },
}
</script>

<style scoped>
.picture-wrapper {
    padding: 10px;
    background-color: #ebeef5;
}
.picture-container {
    height: calc(100vh - 200px);
    overflow-y: auto;
    padding-right: 10px;
}
.picture-column-wrapper {
    display: flex;
    justify-content: space-between;
}
.picture-column {
    width: 200px;
}
.picture-item-container {
    text-align: center;
}
.picture-item {
    min-height: 50px;
    margin-bottom: 8px;
}
.height-max {
    height: 100vh;
}
.picture-load-more {
    display: flex;
    justify-content: center;
    padding: 20px;
}
.picture-item-container.wait-observer {
    min-height: 500px;
}
</style>



