<template>
    <div class="page-header">
        <div class="menu-button"><i class="el-icon-notebook-2"
               @click="switchSidebar()"></i></div>
        <div class="header-container">
            <div>
                <router-link class=btn
                             to="/"><i class="fas fa-vihara"></i> Home</router-link>
            </div>
            <div>
                <a class=btn
                   :href="github_url"
                   target="_blank"><i class="fab fa-github"></i> GitHub</a>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'PageHeader',
    data: function () {
        return {
            github_url: 'https://github.com/onedx1943/onedx1943.github.io',
        }
    },

    methods: {
        switchSidebar: function () {
            document.querySelector('.page-sidebar').classList.toggle('open')
        },
    },
}
</script>

<style scoped>
.page-header {
    position: fixed;
    width: 100%;
    left: 0;
    top: 0;
    z-index: 1500;
    background-color: #409eff;
    background-image: linear-gradient(120deg, #409eff, #53a8ff);
    transition: top 0.4s cubic-bezier(0.4, 0, 0, 1);
    height: 50px;
}
.menu-button {
    display: none;
    position: fixed;
    color: #fff;
    height: 26px;
    width: 26px;
    top: 14px;
    left: 18px;
    transition: top 0.4s cubic-bezier(0.4, 0, 0, 1);
}
.menu-button i {
    cursor: pointer;
}
.header-container {
    height: 100%;
    padding: 0 50px;
    margin: auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.btn {
    color: white;
    display: inline-block;
    padding: 5px 16px;
    font-size: 14px;
    line-height: 18px;
    white-space: nowrap;
    cursor: pointer;
    user-select: none;
    border-radius: 4px;
    text-decoration: none;
    border: 1px solid #ffffff00;
    transition: color 0.2s, background-color 0.2s, border-color 0.2s;
}
.btn:hover {
    color: #ffffffcc;
    background-color: #ffffff33;
    border-color: #ffffff4d;
}

@media screen and (max-height: 600px) {
    .page-header {
        top: -50px;
    }
    .menu-button {
        top: -50px;
    }
}
@media screen and (max-width: 1750px) {
    .menu-button {
        display: block;
    }
}
</style>



