<template>
    <div class="main-content">
        <el-row v-show="showBookshelf">
            <el-col :span="24">
                <div class="novel-bookshelf">
                    <div class="item-novel">
                        <div class="novel-add"
                             @click="dialogAddNovels=true"><i class="el-icon-plus"></i></div>
                        <div class="novel-name"></div>
                    </div>
                    <div v-for="novel in novelList"
                         :key="novel.name"
                         class="item-novel">
                        <div class="item-novel-cover">
                            <el-image class="novel-cover-img"
                                      fit="fill"
                                      :src="novel.img">
                                <div slot="error"
                                     class="novel-cover-txt">
                                    {{ novel.name | getNovelName }}
                                </div>
                            </el-image>
                            <span class="novel-cover-shadow"
                                  @click="openNovel(novel)"></span>
                            <el-popconfirm confirm-button-text='是的'
                                           cancel-button-text='不了'
                                           icon="el-icon-info"
                                           icon-color="red"
                                           @confirm="deleteCacheNovel(novel.name)"
                                           title="确定要删除？">
                                <el-button slot="reference"
                                           plain
                                           type="danger"
                                           size="small"
                                           icon="el-icon-delete"
                                           class="item-novel-delete"></el-button>
                            </el-popconfirm>
                        </div>
                        <div class="novel-name"
                             :title="novel.name">{{ novel.name }}</div>
                    </div>
                </div>
            </el-col>
        </el-row>
        <el-row v-show="!showBookshelf">
            <el-col v-loading="loading"
                    element-loading-text="拼命加载中"
                    element-loading-background="rgba(255, 255, 255, 0.8)">
                <div class="novel-read-operate">
                    <div>
                        <el-button type="primary"
                                   plain
                                   size="small"
                                   icon="el-icon-back"
                                   @click="backToBookshelf">返回书架</el-button>
                        <el-button type="success"
                                   plain
                                   size="small"
                                   v-if="novel_page > 0 && novel_page <= novel_chapter.length"
                                   @click="dialogVisible=true">阅读模式
                        </el-button>
                        <el-button type="danger"
                                   plain
                                   size="small"
                                   :icon="voicePlay ? 'el-icon-video-pause' : 'el-icon-video-play'"
                                   @click="listenBook"></el-button>
                        <el-popover placement="right-start"
                                    width="340"
                                    trigger="click">
                            <el-row>
                                <el-col>
                                    <el-form label-position="right"
                                             label-width="80px"
                                             @submit.native.prevent>
                                        <el-form-item label="人声">
                                            <el-select v-model="voiceType"
                                                       placeholder="请选择"
                                                       size="small">
                                                <el-option v-for="voice in voiceList"
                                                           :key="voice.name"
                                                           :label="voice.name | simpleVoiceName"
                                                           :value="voice">
                                                </el-option>
                                            </el-select>
                                        </el-form-item>
                                        <el-form-item label="语速">
                                            <el-input-number size="small"
                                                             v-model="voiceRate"
                                                             :precision="1"
                                                             :step="0.1"
                                                             :min="0.1"
                                                             :max="10"></el-input-number>
                                        </el-form-item>
                                        <el-form-item label="音高">
                                            <el-input-number size="small"
                                                             v-model="voicePitch"
                                                             :precision="1"
                                                             :step="0.1"
                                                             :min="0"
                                                             :max="2"></el-input-number>
                                        </el-form-item>
                                    </el-form>
                                </el-col>
                            </el-row>
                            <el-button slot="reference"
                                       type="danger"
                                       plain
                                       size="small"
                                       icon="el-icon-headset"
                                       class="popover-btn">
                                听书设置
                            </el-button>
                        </el-popover>
                    </div>
                    <div class="novel-content-setting">
                        <el-popover popper-class="reader-catalog mini-scroll"
                                    placement="left-end"
                                    width="520"
                                    trigger="click"
                                    ref="catalog-popover"
                                    @after-enter="showCurrentChapter">
                            <el-row>
                                <el-col>
                                    <div v-for="(chapter, index) in novel_chapter"
                                         :key="chapter"
                                         class="chapter-item"
                                         :class="novel_page - 1 === index ? 'selected' : ''"
                                         @click="jumpChapter(index)">
                                        {{ index + 1 }}. {{ chapter }}
                                    </div>
                                </el-col>
                            </el-row>
                            <el-button slot="reference"
                                       type="primary"
                                       plain
                                       size="small"
                                       icon="el-icon-tickets"
                                       class="popover-btn">
                                目录
                            </el-button>
                        </el-popover>
                        <el-popover placement="left-start"
                                    width="400"
                                    trigger="click">
                            <div class="novel-setting">
                                <div>字体大小：&nbsp;&nbsp;
                                    <el-select v-model="font_size"
                                               placeholder="请选择"
                                               size="small">
                                        <el-option v-for="item in font_size_options"
                                                   :key="item.value"
                                                   :label="item.label"
                                                   :value="item.value">
                                        </el-option>
                                    </el-select>
                                </div>
                                <div>字体类型：&nbsp;&nbsp;
                                    <div>
                                        <el-radio-group v-model="font_family"
                                                        size="small">
                                            <el-radio-button label="SimSun">宋体</el-radio-button>
                                            <el-radio-button label="Microsoft YaHei">雅黑</el-radio-button>
                                            <el-radio-button label="KaiTi">楷体</el-radio-button>
                                            <el-radio-button label="STXingkai">行楷</el-radio-button>
                                        </el-radio-group>
                                    </div>
                                </div>
                                <div>背景颜色：&nbsp;&nbsp;
                                    <el-color-picker v-model="bg_color"
                                                     show-alpha
                                                     :predefine="predefine_bg_colors"
                                                     size="small">
                                    </el-color-picker>
                                </div>
                                <div>文字颜色：&nbsp;&nbsp;
                                    <el-color-picker v-model="color"
                                                     show-alpha
                                                     :predefine="predefine_colors"
                                                     size="small">
                                    </el-color-picker>
                                </div>
                                <div>内容高度：&nbsp;&nbsp;
                                    <el-input-number v-model="height"
                                                     :controls="false"
                                                     size="small"
                                                     :min="0"
                                                     :max="1200">
                                    </el-input-number>
                                </div>
                            </div>
                            <el-button type="primary"
                                       icon="el-icon-s-tools"
                                       slot="reference"
                                       circle></el-button>
                        </el-popover>
                    </div>
                </div>
                <div class="novel-title">
                    <div class="novel-title-book">{{ novel_name }}</div>
                    <div class="novel-title-chapter">{{ novel_chapter[novel_page - 1] }}</div>
                </div>
                <div class="novel-container">
                    <div class="novel-content mini-scroll"
                         :style="{'background-color': bg_color, 'color': color, 'max-height': height + 'px'}">
                        <pre :style="{'font-size': font_size, 'font-family': font_family, 'line-height': '36px'}">
                            <span>{{ novel_content[novel_page - 1] }}</span>
                        </pre>
                        <div v-if="novel_chapter.length > 1"
                             class="turn_page">
                            <el-button plain
                                       @click="pre_page"
                                       icon="el-icon-arrow-left">{{ novel_chapter[novel_page - 2] | interceptStr }}</el-button>
                            <div>{{ novel_chapter[novel_page - 1] | interceptStr }}</div>
                            <el-button plain
                                       @click="next_page">{{ novel_chapter[novel_page] | interceptStr }}<i class="el-icon-arrow-right el-icon--right"></i></el-button>
                        </div>
                    </div>
                    <div v-if="novel_chapter.length > 1"
                         class="turn_page">
                        <el-button plain
                                   @click="pre_page">上一章</el-button>
                        <div>{{ novel_page }}</div>
                        <el-button plain
                                   @click="next_page">下一章</el-button>
                    </div>
                </div>
            </el-col>
        </el-row>

        <el-dialog title="添加小说"
                   :visible.sync="dialogAddNovels"
                   :append-to-body="true"
                   custom-class="dialog-add-novels">
            <div>
                <input type="file"
                       id="local_file"
                       accept=".txt"
                       style="display: none"
                       @change="uploadLocalFile($event)" />
                <el-button type="primary"
                           plain
                           size="small"
                           class="load-local-btn"
                           @click="selectFile($event)">打开本地文件
                </el-button>
                <div class="novel-custom">
                    <el-input placeholder="请输入内容"
                              v-model="custom_api">
                        <template slot="prepend">自定义github仓库地址</template>
                        <el-button slot="append"
                                   icon="el-icon-search"
                                   @click="loadCustomApi"></el-button>
                    </el-input>
                </div>
                <div class="novel-list">
                    <el-input placeholder="输入关键字进行过滤"
                              v-model="filterText">
                    </el-input>
                    <el-tree class="filter-tree novel-list-content"
                             lazy
                             show-checkbox
                             ref="tree"
                             node-key="id"
                             :load="loadNode"
                             :props="defaultProps"
                             :filter-node-method="filterNode">
                    </el-tree>
                </div>
            </div>
            <div slot="footer"
                 class="dialog-footer">
                <el-button @click="dialogAddNovels=false">取 消</el-button>
                <el-button type="primary"
                           @click="addNovelsConfirm">确 定</el-button>
            </div>
        </el-dialog>

        <el-dialog :visible.sync="dialogVisible"
                   :fullscreen="true"
                   :modal="false"
                   custom-class="novel-dialog">
            <div class="novel-content mini-scroll"
                 :style="{'background-color': bg_color, 'color': color, 'max-height': 'calc(100vh - 80px)'}">
                <pre class=""
                     :style="{'font-size': font_size, 'font-family': font_family, 'line-height': '36px'}">
                    <span>{{ novel_content[novel_page - 1] }}</span>
                </pre>
                <div v-if="novel_chapter.length > 1"
                     class="turn_page">
                    <el-button plain
                               @click="pre_page"
                               icon="el-icon-arrow-left">{{ novel_chapter[novel_page - 2] | interceptStr }}</el-button>
                    <div>{{ novel_chapter[novel_page - 1] | interceptStr }}</div>
                    <el-button plain
                               @click="next_page">{{ novel_chapter[novel_page] | interceptStr }}<i class="el-icon-arrow-right el-icon--right"></i></el-button>
                </div>
            </div>
            <div v-if="novel_chapter.length > 1"
                 class="turn_page">
                <el-button plain
                           @click="pre_page">上一章</el-button>
                <div>{{ novel_page }}</div>
                <el-button plain
                           @click="next_page">下一章</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
export default {
    name: 'AppNovels',
    filters: {
        chapter: function (value) {
            if (value === 0) {
                return ''
            }
            return '第' + value + '页';
        },

        interceptStr: function (value) {
            return (value || '').replaceAll("\r|\n", "").trim().slice(0, 25);
        },

        getNovelName: function (value) {
            let match = new RegExp("《(?<name>[^《》]*)》.*", 'i').exec(value);
            if (match) {
                return match.groups.name;
            }
            return value
        },
        simpleVoiceName: function (value) {
            if (value === 'Microsoft Huihui - Chinese (Simplified, PRC)') {
                return 'Huihui'
            }
            if (value === 'Microsoft Kangkang - Chinese (Simplified, PRC)') {
                return 'Kangkang'
            }
            if (value === 'Microsoft Yaoyao - Chinese (Simplified, PRC)') {
                return 'Yaoyao'
            }
            return value
        }
    },

    data: function () {
        return {
            novel_msg: '正在读取文件...',
            novel_name: '',
            novel_chapter: [],
            novel_content: [],
            novel_page: 0,
            font_size: '18.7px',
            font_size_options: [
                { value: '56px', label: '初号' },
                { value: '48px', label: '小初' },
                { value: '34.7px', label: '一号' },
                { value: '32px', label: '小一' },
                { value: '29.3px', label: '二号' },
                { value: '24px', label: '小二' },
                { value: '21.3px', label: '三号' },
                { value: '20px', label: '小三' },
                { value: '18.7px', label: '四号' },
                { value: '16px', label: '小四' },
                { value: '14px', label: '五号' },
                { value: '12px', label: '小五' },
                { value: '10px', label: '六号' },
                { value: '8.7px', label: '小六' },
                { value: '7.3px', label: '七号' },
                { value: '6.7px', label: '八号' },
            ],
            font_family: 'Microsoft YaHei',
            bg_color: '#ffffff',
            predefine_bg_colors: [
                '#ffffff',
                '#f9f6ed',
                '#f5edd4',
                '#e6f3e9',
                '#e7f4f5',
                '#f5e8e7',
                '#e3e5e3',
                '#181a1b'
            ],
            color: '#262626',
            predefine_colors: [
                '#262626',
                '#666',
            ],
            height: 520,
            custom_api: 'https://api.github.com/repos/onedx1943/Files/contents',
            filterText: '',
            defaultProps: {
                children: 'children',
                label: 'label',
                isLeaf: 'isLeaf'
            },
            node_had: undefined,
            resolve_had: undefined,
            loading: false,
            dialogVisible: false,
            showBookshelf: true,
            novelList: [],
            dialogAddNovels: false,
            synthesis: null,
            voiceList: [],
            voiceType: null,
            voiceRate: 1,
            voicePitch: 1,
            voicePlay: false, // 用于切换播放图标
            readNum: -1,
        }
    },

    watch: {
        filterText: function (val) {
            this.$refs.tree.filter(val.toLowerCase());
        },
        readList: function (val) {
            this.readNum = -1;
        },
        voiceType: function (val) {
            this.debouncedSetSpeak()
        },
        voiceRate: function (val) {
            this.debouncedSetSpeak()
        },
        voicePitch: function (val) {
            this.debouncedSetSpeak()
        },
    },

    computed: {
        readList: function () {
            let read_list = [];
            if (this.novel_page > 0) {
                read_list = this.novel_content[this.novel_page - 1].split(/[\u3000| ]/).filter(item => item !== '');
                read_list.unshift(this.novel_chapter[this.novel_page - 1]);
            }
            return read_list
        },
    },
    created: function () {
        window.addEventListener('beforeunload', this.speakCancel);
        this.debouncedSetSpeak = _.debounce(this.setSpeak, 500)
    },

    mounted: function () {
        let _this = this;

        _this.synthesis = window.speechSynthesis;
        _this.getVoicesList();
        if (window.speechSynthesis.onvoiceschanged !== undefined) {
            window.speechSynthesis.onvoiceschanged = _this.getVoicesList;
        }

        _this.$localforage.getItem('novel_list').then(function (novel_list) {
            if (novel_list === null) {
                _this.novelList = [];
            } else {
                _this.novelList = novel_list;
                _this.novelList.sort(function (a, b) {
                    return b.timestamp - a.timestamp;
                });
            }
        }).catch(function (err) {
            console.log(err);
        });
    },

    beforeDestroy: function () {
        this.speakCancel();
    },

    destroy: function () {
        window.removeEventListener('beforeunload', this.speakCancel);
    },

    methods: {
        loadNode: function (node, resolve) {
            if (node.level === 0 || !node.data.isLeaf) {
                let _this = this;

                let url = '';
                if (node.level === 0) {
                    url = _this.custom_api;
                    _this.node_had = node;
                    _this.resolve_had = resolve;
                } else {
                    url = node.data.url;
                }
                _this.$http.get(url, {
                    headers: {
                        'Authorization': _this.$token1 + _this.$token2 + _this.$token3
                    }
                }).then(function (response) {
                    _this.limitNotification(response.headers);
                    let data = [];
                    for (let i = 0; i < response.data.length; i++) {
                        if (response.data[i].name.endsWith('.txt')) {
                            data.push({
                                label: response.data[i].name + ' (' + (response.data[i].size / 1024 / 1024).toFixed(2) + 'M' + ')',
                                isLeaf: true,
                                url: response.data[i].download_url,
                                id: response.data[i].sha,
                                name: response.data[i].name
                            });
                        } else if (response.data[i].type === 'dir') {
                            data.push({
                                label: response.data[i].name,
                                isLeaf: false,
                                url: response.data[i].url,
                                id: response.data[i].sha,
                                name: response.data[i].name
                            });
                        }
                    }
                    return resolve(data);
                }).catch(function (error) {
                    _this.$notify({
                        type: 'error',
                        title: '错误',
                        message: 'API访问失败，请检查',
                        offset: 60
                    });
                    console.log(error);
                });
            } else {
                return resolve([]);
            }
        },

        clearTree: function () {
            if (this.node_had) {
                this.node_had.childNodes = [];
                this.loadNode(this.node_had, this.resolve_had);
            }
        },

        filterNode: function (value, data) {
            if (!value) return true;
            return data.label.toLowerCase().indexOf(value) !== -1;
        },

        limitNotification: function (headers) {
            let limit = headers['x-ratelimit-limit'];
            let remaining = headers['x-ratelimit-remaining'];
            if (parseInt(remaining) / parseInt(limit) < 0.4) {
                this.$notify({
                    type: 'warning',
                    title: '警告',
                    message: '请求速率限制: ' + remaining + '/' + limit + '每小时',
                    offset: 60
                })
            }
        },

        handleCurrentChange: function (val) {
            this.saveToSession();
        },

        getChapterFromSession: function () {
            if (this.novel_name === '') {
                return
            }
            let _this = this;
            _this.$localforage.getItem('novel_' + this.novel_name).then(function (novel_data) {
                if (novel_data) {
                    _this.novel_page = novel_data.page;
                }
            }).catch(function (err) {
                console.log(err);
            });
        },

        pre_page: function () {
            if (this.novel_page > 1) {
                if (this.readNum > -1) {
                    this.setSpeak();
                }
                this.novel_page -= 1;
                this.saveToSession();
                let _this = this;
                _this.$nextTick(() => {
                    _this.readNum = -1;
                });
            }
        },

        next_page: function () {
            if (this.novel_page < this.novel_chapter.length) {
                if (this.readNum > -1) {
                    this.setSpeak();
                }
                this.novel_page += 1;
                this.saveToSession();
                let _this = this;
                _this.$nextTick(() => {
                    _this.readNum = -1;
                });
            }
        },

        saveToSession: function () {
            let _this = this;
            if (this.novel_name === '') {
                return
            }
            document.querySelector('.novel-content').scrollTop = 0;
            _this.$localforage.getItem('novel_' + _this.novel_name).then(function (novel_data) {
                if (novel_data) {
                    novel_data.page = _this.novel_page;
                    _this.$localforage.setItem('novel_' + _this.novel_name, novel_data).then(function () {
                        _this.$parent.refreshUsage();
                    })
                }
            }).catch(function (err) {
                console.log(err);
            });
        },

        loadCustomApi: function () {
            // 检测链接合法性
            let reg = new RegExp(this.$apiReg, 'i');
            // 清空原有的txt，并重新加载列表
            this.novel_name = '';
            this.novel_chapter = [];
            this.novel_content = [];
            this.novel_page = 0;
            if (reg.test(this.custom_api)) {
                this.clearTree();
            } else {
                this.novel_msg = '别瞎填链接~';
                this.novel_page = 1;
                this.novel_chapter = ['下面是提示信息：'];
                this.novel_content = ['链接格式：https://api.github.com/repos/用户名/仓库名/contents（仓库内具体路径可以接着加 /xxx/xxx）'];
            }
        },
        selectFile: function (event) {
            event.preventDefault();
            event.stopPropagation();
            document.querySelector('#local_file').click();
        },
        uploadLocalFile: function (event) {
            if (this.loading) {
                document.querySelector('#local_file').value = '';
                return this.$notify({
                    type: 'info',
                    title: '提示',
                    message: '请等待当前文件加载完成~',
                    offset: 60
                });
            }
            let files = [];
            try {
                files = event.target.files;
            } catch (e) {
                return
            }
            if (files.length === 0) {
                return
            }
            let file = files[0];
            let _this = this;
            let reader = new FileReader();
            reader.onload = function () {
                let fileData = this.result;
                _this.$localforage.getItem('novel_list').then(function (novel_list) {
                    if (novel_list === null) {
                        novel_list = [];
                    }
                    let index = novel_list.findIndex(item => item.name === file.name);
                    if (index < 0) {
                        novel_list.push({
                            'name': file.name,
                            'url': '',
                            'img': 'data:image/jpeg;base64,',
                            'timestamp': new Date().getTime()
                        })
                    }
                    _this.novelList = novel_list;
                    _this.novelList.sort(function (a, b) {
                        return b.timestamp - a.timestamp;
                    });
                    return _this.$localforage.setItem('novel_list', novel_list)
                }).then(function () {
                    _this.$parent.refreshUsage();
                    return _this.$localforage.setItem('novel_' + file.name, {
                        content: fileData,
                        page: 1
                    });
                }).then(function () {
                    _this.$parent.refreshUsage();
                    _this.dialogAddNovels = false;
                    document.querySelector('#local_file').value = '';
                }).catch(function (err) {
                    console.log(err);
                    document.querySelector('#local_file').value = '';
                });
            };
            reader.readAsText(file);
        },
        addNovelsConfirm: function () {
            let _this = this;
            let checked = _this.$refs.tree.getCheckedNodes();
            _this.$localforage.getItem('novel_list').then(function (novel_list) {
                if (novel_list === null) {
                    novel_list = [];
                }
                for (let novel of checked) {
                    if (novel.isLeaf) {
                        let index = novel_list.findIndex(item => item.name === novel.name);
                        if (index < 0) {
                            novel_list.push({
                                'name': novel.name,
                                'url': novel.url,
                                'img': 'data:image/jpeg;base64,',
                                'timestamp': new Date().getTime()
                            })
                        }
                    }
                }
                _this.novelList = novel_list;
                _this.novelList.sort(function (a, b) {
                    return b.timestamp - a.timestamp;
                });
                return _this.$localforage.setItem('novel_list', novel_list)
            }).then(function () {
                _this.$parent.refreshUsage();
                _this.dialogAddNovels = false
            }).catch(function (err) {
                console.log(err);
            });
        },
        openNovel: function (novel) {
            let _this = this;
            _this.loading = true;
            _this.novel_name = novel.name;
            _this.novel_chapter = ['提示：'];
            _this.novel_content = ['正在读取...'];
            _this.novel_page = 1;
            _this.showBookshelf = false;
            _this.readNum = -1;
            _this.updateTimestamp(novel.name);
            let key = 'novel_' + novel.name;
            _this.$localforage.getItem(key).then(function (novel_data) {
                if (novel_data === null) {
                    _this.downloadNovel(novel);
                } else {
                    _this.loadNovelContent(novel_data.content, novel_data.page);
                }
            }).catch(function (err) {
                console.log(err);
            });
        },
        downloadNovel: function (novel) {
            let _this = this;
            _this.$http.get(novel.url).then(function (response) {
                let novel_data = {
                    content: response.data,
                    page: 1
                };
                _this.$localforage.setItem('novel_' + novel.name, novel_data).then(function () {
                    _this.$parent.refreshUsage();
                    _this.loadNovelContent(response.data, 1);
                });
            }).then(function () {
                _this.getNovelCover(novel)
            }).catch(function (error) {
                console.log(error);
                _this.novel_content = ['取文件失败喽！'];
                _this.$notify({
                    type: 'error',
                    title: '错误',
                    message: '获取文件失败，请检查',
                    offset: 60
                });
                _this.loading = false;
            });
        },
        loadNovelContent: function (data, page) {
            let _this = this;
            if (data === '') {
                _this.novel_content = ['该文件内容为空，还是看看别的吧'];
                _this.loading = false;
                return
            }
            let reg = /\n\s*[第]{1,2}[0-9零○一二两三四五六七八九十百千廿卅卌壹贰叁肆伍陆柒捌玖拾佰仟万１２３４５６７８９０]{1,5}[章节節堂讲回集部分品]{1,2}.*/g;
            let chapter = data.match(reg);
            if (chapter) {
                chapter.unshift(_this.novel_name);
            } else {
                chapter = [_this.novel_name];
                if (data.length > 1024 * 1024) {
                    _this.novel_content = ['没找到章节名，而且内容还挺大，是不是文件编码有问题'];
                    _this.loading = false;
                    return
                }
            }
            _this.novel_chapter = chapter;
            _this.novel_content = data.split(reg);
            _this.novel_page = page;
            _this.loading = false;
        },
        deleteCacheNovel: function (name) {
            let _this = this;
            _this.$localforage.getItem('novel_list').then(function (novel_list) {
                novel_list.splice(novel_list.findIndex(item => item.name === name), 1);
                _this.$localforage.setItem('novel_list', novel_list).then(function (value) {
                    _this.$parent.refreshUsage();
                    _this.novelList = novel_list;
                    _this.novelList.sort(function (a, b) {
                        return b.timestamp - a.timestamp;
                    });
                });
                _this.$localforage.removeItem('novel_' + name).then(function () {
                    _this.$parent.refreshUsage();
                });
            }).catch(function (err) {
                // 当出错时，此处代码运行
                console.log(err);
            });
        },
        updateTimestamp: function (name) {
            let _this = this;
            _this.$localforage.getItem('novel_list').then(function (novel_list) {
                let index = novel_list.findIndex(item => item.name === name);
                novel_list[index]['timestamp'] = new Date().getTime();
                _this.$localforage.setItem('novel_list', novel_list).then(function () {
                    _this.$parent.refreshUsage();
                    _this.novelList = novel_list;
                    _this.novelList.sort(function (a, b) {
                        return b.timestamp - a.timestamp;
                    });
                });
            }).catch(function (err) {
                // 当出错时，此处代码运行
                console.log(err);
            });
        },
        getNovelCover: function (novel) {
            let _this = this;
            let img_url = novel.url.slice(0, novel.url.length - 3) + 'jpg';
            _this.getBase64ImageUrl(img_url, 'jpg', function (base64) {
                _this.$localforage.getItem('novel_list').then(function (novel_list) {
                    let index = novel_list.findIndex(item => item.name === novel.name);
                    novel_list[index]['img'] = base64;
                    _this.$localforage.setItem('novel_list', novel_list).then(function () {
                        _this.$parent.refreshUsage();
                        _this.novelList = novel_list;
                        _this.novelList.sort(function (a, b) {
                            return b.timestamp - a.timestamp;
                        });
                    });
                });
            });
        },
        getBase64ImageUrl: function (url, ext, callback) {
            let xhr = new XMLHttpRequest();
            xhr.responseType = 'arraybuffer';
            xhr.open('GET', url, true);
            xhr.onload = function () {
                let result = xhr.response;
                let file = new File([result], 'image.' + ext, {
                    type: 'image/' + ext,
                });
                let reader = new FileReader();
                reader.onload = function (evt) {
                    callback(evt.target.result);
                };
                reader.readAsDataURL(file)
            };
            xhr.send(null);
        },
        backToBookshelf: function () {
            this.showBookshelf = true;
            this.speakCancel();
        },
        getVoicesList: function () {
            this.voiceList = this.synthesis.getVoices().filter(item => item.lang.startsWith('zh-') && item.localService);
            if (this.voiceList.length > 0) {
                this.voiceType = this.voiceList[0];
            }
        },
        speakStart: function (value) {
            let _this = this;
            if (this.synthesis.speaking) {
                console.error('speechSynthesis.speaking');
                return;
            }
            if (value !== '') {
                let utterance = new SpeechSynthesisUtterance(value);
                utterance.onend = function (event) {
                    if (_this.voicePlay) {
                        _this.readNum++;
                        if (_this.readNum < 0) {
                            _this.readNum = 0;
                        }
                        if (_this.readNum === _this.readList.length) {
                            _this.next_page();
                        }
                        _this.$nextTick(() => {
                            _this.listenBook();
                        });
                    }
                };
                utterance.onerror = function (event) {
                    console.error('SpeechSynthesisUtterance.onerror');
                };
                utterance.voice = this.voiceType;
                utterance.pitch = this.voicePitch;
                utterance.rate = this.voiceRate;
                this.synthesis.speak(utterance);
            }
        },
        speakCancel: function () {
            this.voicePlay = false;
            if (this.synthesis) {
                this.synthesis.cancel();
            }
        },
        listenBook: function () {
            if (this.synthesis.paused && this.synthesis.speaking) {
                this.synthesis.resume();
                this.voicePlay = true;
            } else if (this.synthesis.speaking) {
                this.synthesis.pause();
                this.voicePlay = false;
            } else {
                if (this.readNum < 0) {
                    this.readNum = 0;
                }
                this.speakStart(this.readList[this.readNum]);
                this.voicePlay = true;
            }
        },
        jumpChapter: function (page_num) {
            this.$refs['catalog-popover'].doClose();
            this.novel_page = page_num + 1;
            this.saveToSession();
        },
        showCurrentChapter: function () {
            document.querySelector('.reader-catalog').scrollTop = document.querySelector('.chapter-item.selected').offsetTop;
        },
        setSpeak: function () {
            if (this.voicePlay) {
                this.readNum--;
            }
            if (this.synthesis) {
                this.synthesis.cancel();
            }
        },
    },
}
</script>

<style scoped>
.novel-bookshelf {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: flex-start;
    margin-left: -36px;
}
.item-novel {
    width: 128px;
    margin-left: 36px;
    margin-bottom: 44px;
}
.novel-add {
    height: 185px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 32px;
    background-color: #edeeef;
    cursor: pointer;
    user-select: none;
}
.item-novel-cover {
    display: flex;
    box-shadow: 0 2px 16px #00000014;
    background: #d8d8d8;
    position: relative;
    cursor: pointer;
    user-select: none;
}
.item-novel-delete {
    position: absolute;
    top: 0;
    right: 0;
    color: #f56c6c;
    font-size: 15px;
    background-color: #ffffff4d;
    border: none;
    padding: 4px;
    visibility: hidden;
}
.novel-cover-img {
    width: 100%;
    height: 185px;
}
.novel-cover-shadow {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-image: linear-gradient(
        90deg,
        #a1a1a140,
        #1515141a 1%,
        #ffffff26 4%,
        #9494941a 8%,
        #e3e3e300 57%,
        #dfdada08 91%,
        #dfdada0d 98%,
        #ffffff1a
    );
    box-shadow: inset 0 0 0 0 #0000001a;
}
.novel-cover-shadow:hover + span .item-novel-delete {
    visibility: visible;
}
.item-novel-delete:hover {
    visibility: visible;
}
.novel-name {
    margin-top: 14px;
    font-size: 15px;
    color: #5d646e;
    line-height: 18px;
    overflow: hidden;
    display: -webkit-box;
    text-overflow: ellipsis;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    -webkit-text-size-adjust: none;
    height: 36px;
    max-height: 36px;
}
.novel-read-operate {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 12px;
}
.novel-content-setting {
    margin: 0 0 0 auto;
}
.popover-btn {
    margin-left: 10px;
}
.novel-title {
    font-size: 16px;
    display: flex;
    height: 36px;
    line-height: 36px;
    padding: 10px 0;
    border-bottom: 1px solid #ebeef5;
}
.novel-title-book {
    min-width: 150px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}
.novel-title-chapter {
    margin-left: 36px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}
.chapter-item {
    height: 52px;
    line-height: 52px;
    border-radius: 0;
    border: solid #ebedf1;
    border-width: 0 0 1px;
    padding: 0 16px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    cursor: pointer;
}
.chapter-item:hover {
    background: linear-gradient(90deg, #0000000d, transparent);
}
.chapter-item.selected {
    color: #1b88ee;
}
.novel-custom {
    display: flex;
    justify-content: space-between;
    padding: 10px 20px;
    border-bottom: 1px dotted black;
    margin-bottom: 10px;
}
.novel-custom span {
    display: inline-block;
    height: 38px;
    line-height: 38px;
    padding-right: 10px;
    overflow: hidden;
}
.novel-custom input {
    flex-grow: 1;
}
.novel-list {
    padding: 0 20px;
}
.novel-list-content {
    height: calc(100vh - 600px);
    min-height: 200px;
    overflow-y: auto;
    overflow-y: overlay;
    margin: 10px;
    padding-right: 17px;
}
.novel-setting {
    display: flex;
    flex-flow: column nowrap;
    justify-content: center;
    align-items: center;
    margin: 10px;
}
.novel-setting > div {
    width: 300px;
    overflow: hidden;
    margin: 3px;
    display: flex;
    height: 32px;
    align-items: center;
}
.novel-setting > div select,
.novel-setting > div input {
    width: 221px;
}
.el-popper[x-placement^='bottom'] {
    margin-top: 6px !important;
}
.novel-setting .el-input-number__decrease,
.novel-setting .el-input-number__increase {
    display: none;
}
.novel-setting label {
    margin-bottom: 0;
}
.novel-content {
    padding: 0 50px;
    overflow: auto;
}
.novel-content > pre {
    width: 100%;
    word-break: break-word;
    white-space: break-spaces;
    margin: 0;
}
.turn_page {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 20px;
    margin-top: 20px;
}
.load-local-btn {
    margin: 0 28px;
}
</style>



