import Vue from 'vue'
import Router from 'vue-router'

// 1. 定义 (路由) 组件。引入组件。
import Home from '@/components/Home'
import App from '@/components/App'
import AppIndex from '@/components/AppIndex'
import AppIcon from '@/components/AppIcon'
import AppNovels from '@/components/AppNovels'
import AppMusic from '@/components/AppMusic'
import AppPicture from '@/components/AppPicture'
import AppVideo from '@/components/AppVideo'
import AppWorkHour from '@/components/AppWorkHour'

import Blog from '@/components/Blog'


Vue.use(Router);


export default new Router({
  routes: [
    {path: '/', component: Home},
    {
      path: '/app', component: App,
      children: [
        {path: '/', component: AppIndex},
        {path: 'icon', component: AppIcon},
        {path: 'novel', component: AppNovels},
        {path: 'music', component: AppMusic},
        {path: 'picture', component: AppPicture},
        {path: 'video', component: AppVideo},
        {path: 'work-hour', component: AppWorkHour},
      ]
    },
    {path: '/blog', component: Blog},
  ]
})



