<template>
    <div class="main-content">
        {{ msg }}
        <div class="app-index">
            <el-card v-for="app in apps"
                     :key="app.name"
                     shadow="hover"
                     :body-style="{ padding: '0px' }">
                <div @click="toPage(app.link)"
                     class="app-card">{{ app.name }}</div>
            </el-card>
        </div>
    </div>
</template>

<script>
export default {
    name: 'AppIndex',
    data: function () {
        return {
            apps: [
                { name: 'Icon', link: '/app/icon' },
                { name: 'Novels', link: '/app/novel' },
                { name: 'Music', link: '/app/music' },
                { name: 'Picture', link: '/app/picture' },
                { name: 'Video', link: '/app/video' },
                { name: 'Work Hour', link: '/app/work-hour' },
            ],
            msg: '欢迎来到 App 首页',
        }
    },

    methods: {
        toPage: function (link) {
            this.$router.push(link)
        }
    },
}
</script>

<style scoped>
.app-index {
    display: grid;
    grid-template-columns: repeat(3, 33.33%);
    gap: 20px;
    place-content: center;
    margin: 20px auto;
}
.app-card {
    padding: 20px;
}
</style>



