<template>
    <div class="page-container">
        <pageHeader></pageHeader>
        <div class="page-app">
            <div class="app-container">
                <div class="page-sidebar">
                    <div class="page-sidebar-inner">
                        <el-menu :default-active="activeMenu"
                                 class="app-container-menu"
                                 @select="menuItemSelect"
                                 :router='true'>
                            <el-menu-item index="/app/icon">
                                <i class="el-icon-sugar"></i>
                                <span slot="title">Icon</span>
                            </el-menu-item>
                            <el-menu-item index="/app/novel">
                                <i class="el-icon-reading"></i>
                                <span slot="title">Novels</span>
                            </el-menu-item>
                            <el-menu-item index="/app/music">
                                <i class="el-icon-headset"></i>
                                <span slot="title">Music</span>
                            </el-menu-item>
                            <el-menu-item index="/app/picture">
                                <i class="el-icon-picture-outline"></i>
                                <span slot="title">Picture</span>
                            </el-menu-item>
                            <el-menu-item index="/app/video">
                                <i class="el-icon-monitor"></i>
                                <span slot="title">Video</span>
                            </el-menu-item>
                            <el-menu-item index="/app/work-hour">
                                <i class="el-icon-date"></i>
                                <span slot="title">Work Hour</span>
                            </el-menu-item>
                        </el-menu>
                    </div>
                    <div class="usage-label">当前站点使用磁盘空间: {{ usage }} M</div>
                </div>
                <div class="app-item-content mini-scroll"
                     @click="menuItemSelect">
                    <router-view></router-view>
                </div>
            </div>
            <el-backtop target=".app-item-content"
                        :visibility-height="1000"></el-backtop>
        </div>
    </div>
</template>

<script>
import pageHeader from './PageHeader.vue';

export default {
    components: { pageHeader },

    data: function () {
        return {
            activeMenu: null,
            usage: ""
        };
    },

    created: function () {
        this.activeMenu = this.$router.currentRoute.fullPath;
    },

    mounted: function () {
        this.refreshUsage();
    },

    methods: {
        switchSidebar: function () {
            document.querySelector('.page-sidebar').classList.toggle('open')
        },
        menuItemSelect (key, keyPath) {
            document.querySelector('.page-sidebar').classList.remove('open')
        },
        refreshUsage: function () {
            let _this = this;
            window.navigator.storage.estimate().then(function (estimate) {
                _this.usage = (estimate.usage / 1024 / 1024).toFixed(2);
            });
        }
    }
};
</script>

<style scoped>
.page-container {
    height: 100%;
    overflow: hidden;
}
.page-app {
    padding: 0;
    margin-top: 0;
    height: 100%;
    min-height: auto;
}
.app-container {
    height: calc(100% - 50px);
    margin-top: 50px;
    transition: margin-top 0.4s cubic-bezier(0.4, 0, 0, 1),
        height 0.4s cubic-bezier(0.4, 0, 0, 1);
    position: relative;
    width: 100%;
    overflow: hidden;
}
.page-sidebar {
    position: fixed;
    z-index: 99;
    top: 50px;
    left: 0;
    bottom: 0;
    overflow-x: hidden;
    overflow-y: auto;
    overflow-y: overlay;
    background-color: #f9f9f9;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
    transition: all 0.4s cubic-bezier(0.4, 0, 0, 1);
    text-align: left;
}
.page-sidebar-inner {
    width: 300px;
    padding-top: 10px;
}
.app-container-menu {
    background-color: #f9f9f9;
    user-select: none;
}
.app-item-content {
    position: relative;
    padding-left: 300px;
    transition: padding-left 0.4s cubic-bezier(0.4, 0, 0, 1),
        height 0.4s cubic-bezier(0.4, 0, 0, 1);
    height: 100%;
    overflow: auto;
}
@media screen and (max-height: 600px) {
    .app-container {
        margin-top: 0;
        height: 100%;
    }
    .page-sidebar {
        top: 0;
    }
}
@media screen and (max-width: 1750px) {
    .page-sidebar {
        transform: translate(-300px, 0);
    }
    .app-item-content {
        padding-left: 0;
    }
    .page-sidebar.open {
        transform: translate(0, 0);
    }
}
.scroll-top-button {
    display: none;
    cursor: pointer;
    position: fixed;
    line-height: 40px;
    bottom: 50px;
    right: 50px;
    color: #fff;
    background-color: rgba(0, 0, 0, 0.6);
    padding: 0 8px;
    transition: background-color 0.2s ease;
    border-radius: 4px;
    z-index: 9999;
}
.scroll-top-button > span {
    display: inline-flex;
    height: 24px;
    line-height: 24px;
    width: 24px;
    justify-content: center;
    align-items: center;
}
.usage-label {
    position: fixed;
    bottom: 32px;
    left: 32px;
    z-index: 1;
    font-size: 14px;
    color: #409eff;
    user-select: none;
}
</style>



