<template>
    <div class="page-container">
        <pageHeader></pageHeader>
        <div class="page-blog">
            <div class="blog-container">
                <div>这是Blog页</div>
                <div>{{ msg }}</div>
            </div>
        </div>
    </div>
</template>

<script>
import pageHeader from './PageHeader.vue';

export default {
    name: 'Blog',

    components: { pageHeader },

    data: function () {
        return {
            msg: '瞅啥呢，这里啥也没有啊 ʅ（´◔౪◔）ʃ',
        }
    },

    methods: {},
}
</script>

<style scoped>
.page-container {
    height: 100%;
    overflow: hidden;
}
.page-blog {
    padding: 0;
    margin-top: 0;
    height: 100%;
    min-height: auto;
}
.blog-container {
    height: calc(100% - 50px);
    margin-top: 50px;
    transition: margin-top 0.4s cubic-bezier(0.4, 0, 0, 1);
}
@media screen and (max-height: 600px) {
    .blog-container {
        margin-top: 0;
    }
}
</style>



