<script>
let jsmediatags = require("jsmediatags");

export default {
    name: 'AppMusic',
    filters: {
        formatSeconds: function (value) {
            let time_list = [];//Hour minute second
            let hour = Math.floor(value / 3600);
            if (hour !== 0) {
                time_list.push(hour > 9 ? hour : '0' + hour);
            }
            let minute = Math.floor((value % 3600) / 60);
            time_list.push(minute > 9 ? minute : '0' + minute);
            let second = value % 60;
            time_list.push(second > 9 ? second : '0' + second);
            return time_list.join(':')
        },
        showPlayListName: function (value) {
            return value.slice(4)
        },
        showDownloadName: function (value) {
            return value.slice(9)
        },
        formatSize: function (value) {
            return (value / 1024 / 1024).toFixed(2) + 'M';
        }
    },

    data: function () {
        let validateName = (rule, value, callback) => {
            let reg = new RegExp(/^[\u4e00-\u9fa5\w-]+$/, 'i');
            if (value === '') {
                callback();
            } else if (reg.test(value)) {
                callback();
            } else {
                callback(new Error('输的啥啊，不给创建'));
            }
        };

        return {
            music_list: [],
            music_msg: '正在读取文件...',
            tips_msg: '',
            audioContext: null,
            source: null, //the audio source
            animationId: null,
            status: 0, //flag for sound is playing 1 or stopped 0 suspend 2
            forceStop: false,
            allCapsReachBottom: false,
            play_model: 0, // 列表循环 0 单曲循环 1 随机播放 2
            play_num: 0,
            play_name: '',
            timer: null,
            start_time: 0,
            duration_time: 0,
            current_time: 0,
            percentage: 0,
            volume: 5,
            volume_actual: 5,
            muted: false,
            gainNode: null,
            imgUrl: 'data:image/jpeg;base64,',
            jsMediaTags: jsmediatags,
            musicTitle: '',
            musicAlbum: '',
            musicArtist: '',
            lyricsList: [],
            lyricsContent: [],
            musicLyricsMsg: '',

            activeNames: ['1', '2'],
            play_list: [],  // 播放列表
            playlist_own: [],  // 我的歌单列表
            playlist_collection: [],  // 收藏的歌单列表
            playlist_content: [], // 歌单内容
            search_music: '',
            request_count: 0, // axios请求计数
            playlist_select_name: '',
            playlist_select_api: '',
            fullscreenLoading: false,
            dialogCollectionVisible: false,
            collectionForm: {
                name: '',
                username: 'onedx1943',
                repo: 'musicLowBitRate',
                path: '',
            },
            collectionFormRules: {
                name: [
                    { required: true, message: '请输入歌单名称', trigger: 'change' },
                    { validator: validateName, trigger: 'change' }
                ],
                username: [
                    { required: true, message: '请输入用户名称', trigger: 'change' },
                    { validator: validateName, trigger: 'change' }
                ],
                repo: [
                    { required: true, message: '请输入仓库名称', trigger: 'change' },
                    { validator: validateName, trigger: 'change' }
                ],
                path: [],
            },
            download_music_list: [],
            downloading_list: [],
            playlist_img: 'data:image/jpeg;base64,',
            error_img: 'https://raw.githubusercontent.com/onedx1943/Picture/master/png/sticker42.png',
            show_lyrics_board: false,
            lyrics_api: 'https://api.github.com/repos/onedx1943/Lyrics/contents',
        }
    },

    watch: {},

    computed: {
        download_table: function () {
            let table_list = [];
            for (let name of this.download_music_list) {
                table_list.push({ name: name })
            }
            return table_list
        },
        background_img: function () {
            return `background-image: url(${this.imgUrl})`;
        },
    },

    mounted: function () {
        let _this = this;
        //fix browser vender for AudioContext and requestAnimationFrame
        window.AudioContext = window.AudioContext || window.webkitAudioContext || window.mozAudioContext || window.msAudioContext;
        window.requestAnimationFrame = window.requestAnimationFrame || window.webkitRequestAnimationFrame || window.mozRequestAnimationFrame || window.msRequestAnimationFrame;
        window.cancelAnimationFrame = window.cancelAnimationFrame || window.webkitCancelAnimationFrame || window.mozCancelAnimationFrame || window.msCancelAnimationFrame;

        this.getMusicLyrics(this.lyrics_api);

        _this.$localforage.getItem('volume').then(function (value) {
            if (value !== null) {
                _this.volume = value;
            }
        });

        // 加载歌单列表
        _this.$localforage.keys().then(function (keys) {
            if (keys === null) {
                keys = [];
            }
            for (let name of keys) {
                if (name.startsWith('download_')) {
                    _this.download_music_list.push(name);
                }
            }
            return _this.$localforage.getItem('playlist_own')
        }).then(function (value) {
            if (value === null) {
                let default_list = [{
                    name: 'own_我喜欢的音乐',
                    address: '',
                }];
                _this.$localforage.setItem('playlist_own', default_list).then(function () {
                    _this.$parent.refreshUsage();
                });
                _this.playlist_own = default_list;
            } else if (value.length > 0) {
                _this.playlist_own = value;
            }
            return _this.$localforage.getItem('playlist_collection')
        }).then(function (value) {
            if (value === null) {
                let default_list = [{
                    name: 'clt_onedx的歌单',
                    address: 'https://api.github.com/repos/onedx1943/musicLowBitRate/contents',
                }];
                _this.$localforage.setItem('playlist_collection', default_list).then(function () {
                    _this.$parent.refreshUsage();
                });
                _this.playlist_collection = default_list;
            } else if (value.length > 0) {
                _this.playlist_collection = value;
            }
        }).catch(function (err) {
            // 当出错时，此处代码运行
            console.log(err);
        });
    },

    beforeDestroy: function () {
        // 组件销毁前结束播放
        this.stopMusic(true);
        if (this.audioContext != null) {
            this.audioContext.close();
            this.audioContext = null;
        }
    },

    methods: {
        addPlaylistOwn: function (event) {
            event.preventDefault();
            event.stopPropagation();
            let _this = this;
            _this.$prompt('', '新建歌单', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                inputPlaceholder: '请输入新歌单标题',
                inputPattern: /^[\u4e00-\u9fa5\w-]+$/,
                inputErrorMessage: '求求你输入点正常的东西吧'
            }).then(({ value }) => {
                _this.$localforage.getItem('playlist_own').then(function (playlist) {
                    if (playlist === null) {
                        playlist = [];
                    }
                    let name = 'own_' + value;
                    for (let item of playlist) {
                        if (item.name === name) {
                            _this.$message({
                                type: 'info',
                                message: '歌单已存在: ' + value
                            });
                            return false
                        }
                    }
                    playlist.push({
                        name: name,
                        address: '',
                    });
                    _this.playlist_own = playlist;
                    return _this.$localforage.setItem('playlist_own', playlist);
                }).then(function () {
                    _this.$parent.refreshUsage();
                }).catch(function (err) {
                    // 当出错时，此处代码运行
                    console.log(err);
                });
            }).catch((err) => {
                console.log(err);
            });
        },
        addPlaylistCollection: function (event) {
            event.preventDefault();
            event.stopPropagation();
            // 添加收藏
            this.dialogCollectionVisible = true;
        },
        addPlaylistCollectionConfirm: function () {
            let _this = this;
            this.$refs['collectionForm'].validate((valid) => {
                if (valid) {
                    // 检测链接合法性
                    let reg = new RegExp(this.$apiReg, 'i');
                    let api = `https://api.github.com/repos/${_this.collectionForm.username}/${_this.collectionForm.repo}/contents${_this.collectionForm.path}`;
                    if (reg.test(api)) {
                        _this.$localforage.getItem('playlist_collection').then(function (playlist) {
                            if (playlist === null) {
                                playlist = [];
                            }
                            let name = 'clt_' + _this.collectionForm.name;
                            for (let item of playlist) {
                                if (item.name === name) {
                                    _this.$message({
                                        type: 'info',
                                        message: '歌单已存在: ' + _this.collectionForm.name
                                    });
                                    return false
                                }
                            }
                            playlist.push({
                                name: name,
                                address: api,
                            });
                            _this.playlist_collection = playlist;
                            _this.addPlaylistCollectionCancel();
                            return _this.$localforage.setItem('playlist_collection', playlist);
                        }).then(function () {
                            _this.$parent.refreshUsage();
                        }).catch(function (err) {
                            // 当出错时，此处代码运行
                            console.log(err);
                        });
                    } else {
                        _this.$notify.error({
                            title: '错误',
                            message: '歌单地址不对吧？'
                        });
                        return false;
                    }
                } else {
                    return false;
                }
            });
        },
        addPlaylistCollectionCancel: function () {
            this.dialogCollectionVisible = false;
            this.$refs['collectionForm'].resetFields();
        },

        showPlaylist: function (event, name, api) {
            if (document.querySelector('.item-playlist.selected')) {
                document.querySelector('.item-playlist.selected').classList.remove('selected');
            }
            event.currentTarget.classList.add('selected');
            let _this = this;
            _this.search_music = '';
            _this.playlist_select_name = name;
            _this.playlist_select_api = api;
            _this.playlist_content = [];
            _this.fullscreenLoading = true;
            _this.playlist_img = 'data:image/jpeg;base64,';
            _this.$localforage.getItem(name).then(function (playlist) {
                if (playlist === null) {
                    if (name.startsWith('clt_')) {
                        _this.syncPlaylist(name, api);
                    } else {
                        _this.$localforage.setItem(name, []).then(function () {
                            _this.$parent.refreshUsage();
                        });
                        _this.fullscreenLoading = false;
                    }
                } else {
                    _this.playlist_content = playlist;
                    _this.$localforage.keys().then(function (keys) {
                        if (keys === null) {
                            keys = [];
                        }
                        for (let music of _this.playlist_content) {
                            if (keys.includes('img_' + music.name)) {
                                return _this.$localforage.getItem('img_' + music.name)
                            }
                        }
                    }).then(function (value) {
                        if (value) {
                            _this.playlist_img = value
                        }
                    });
                    _this.$nextTick(() => {
                        _this.fullscreenLoading = false;
                    });
                }
            }).catch(function (err) {
                // 当出错时，此处代码运行
                console.log(err);
                _this.fullscreenLoading = false;
            });
        },

        syncPlaylist: function (name, api) {
            this.fullscreenLoading = true;
            this.playlist_content = [];
            this.getMusicList(api);
            this.updatePlaylist(name);
        },

        updatePlaylist: function (name) {
            let _this = this;
            if (this.request_count > 0) {
                setTimeout(() => {
                    _this.updatePlaylist(name)
                }, 100);
            } else {
                _this.$localforage.setItem(name, _this.playlist_content).then(function (value) {
                    _this.fullscreenLoading = false;
                    _this.$parent.refreshUsage();
                });
            }
        },

        getMusicList: function (file_api) {
            let _this = this;
            _this.request_count++;
            _this.$http.get(file_api, {
                headers: {
                    'Authorization': _this.$token1 + _this.$token2 + _this.$token3
                }
            }).then(function (response) {
                _this.limitNotification(response.headers);
                for (let i = 0; i < response.data.length; i++) {
                    if (response.data[i].name.endsWith('.mp3') || response.data[i].name.endsWith('.flac')) {
                        _this.playlist_content.push(response.data[i])
                    } else if (response.data[i].type === 'dir') {
                        let new_url = file_api + '/' + response.data[i].name;
                        _this.getMusicList(new_url);
                    }
                }
                _this.request_count--;
            }).catch(function (error) {
                console.log(error);
                _this.music_msg = '网络或者链接有问题啊，读取失败了！';
                _this.request_count--;
            });
        },

        deletePlaylist: function (name) {
            let _this = this;
            let list_name = '';
            if (name.startsWith('own_')) {
                list_name = 'playlist_own'
            } else {
                list_name = 'playlist_collection'
            }
            _this.$localforage.getItem(list_name).then(function (playlist) {
                playlist.splice(playlist.findIndex(item => item.name === name), 1);
                _this.$localforage.setItem(list_name, playlist).then(function (value) {
                    _this.playlist_select_name = '';
                    _this.playlist_select_api = '';
                    _this.playlist_content = [];
                    if (name.startsWith('own_')) {
                        _this.playlist_own = playlist;
                    } else {
                        _this.playlist_collection = playlist;
                    }
                    _this.$parent.refreshUsage();
                });
                _this.$localforage.removeItem(name).then(function () {
                    _this.$parent.refreshUsage();
                });
            }).catch(function (err) {
                // 当出错时，此处代码运行
                console.log(err);
            });
        },

        startPlaylist: function () {
            this.stopMusic(true);
            this.play_num = 0;
            this.music_list = this.playlist_content;
            this.$nextTick(() => {
                this.playMusic()
            });
        },

        addToPlay: function (data) {
