        hasDownload: function (name) {
            return this.download_music_list.includes('download_' + name)
        },

        downloadMusic: function (event, data) {
            let _this = this;
            let save_name = 'download_' + data.name;
            event.currentTarget.classList.remove('el-icon-download');
            event.currentTarget.classList.add('el-icon-loading');
            if (this.downloading_list.includes(save_name)) {
                return _this.$message({
                    type: 'info',
                    message: data.name + '正在下载了'
                });
            }
            _this.downloading_list.push(save_name);
            _this.$http({
                method: 'get',
                url: data.download_url,
                responseType: 'arraybuffer',
            }).then(function (response) {
                data.array_buffer = response.data;
                _this.$localforage.setItem(save_name, data).then(function (value) {
                    if (!_this.download_music_list.includes(save_name)) {
                        _this.download_music_list.push(save_name);
                    }
                    _this.downloading_list.splice(_this.downloading_list.findIndex(item => item === save_name), 1);
                    _this.$parent.refreshUsage();
                });
            }).catch(function (error) {
                console.log(error);
                _this.$notify({
                    type: 'error',
                    title: '错误',
                    message: '下载失败咯',
                    offset: 60
                });
                _this.downloading_list.splice(_this.downloading_list.findIndex(item => item === save_name), 1);
                event.currentTarget.classList.remove('el-icon-loading');
                event.currentTarget.classList.add('el-icon-download');
            });

        },

        showDownloadList: function (event, name, api) {
            if (document.querySelector('.item-playlist.selected')) {
                document.querySelector('.item-playlist.selected').classList.remove('selected');
            }
            event.currentTarget.classList.add('selected');
            let _this = this;
            _this.search_music = '';
            _this.playlist_select_name = name;
            _this.playlist_select_api = api;
            _this.fullscreenLoading = true;
            _this.$localforage.keys().then(function (keys) {
                if (keys === null) {
