        deleteCacheFile: function (prefix) {
            let _this = this;
            _this.$localforage.keys().then(function (keys) {
                if (keys === null) {
                    keys = [];
                }
                for (let name of keys) {
                    if (name.startsWith(prefix)) {
                        _this.$localforage.removeItem(name).then(function () {
                            _this.$parent.refreshUsage();
                        });
                    }
                }
                if (prefix === 'download_') {
                    _this.download_music_list = [];
                }
            }).catch(function (err) {
                console.log(err);
            });
