        getMusicContent: function (event, music_data) {
            let _this = this;
            let name = music_data.name;
            _this.imgUrl = 'data:image/jpeg;base64,';
            _this.musicTitle = '';
            _this.musicAlbum = '';
            _this.musicArtist = '';
            _this.musicLyricsMsg = '';
            _this.lyricsContent = [];
            if (document.querySelector('.music-list .active-music')) {
                document.querySelector('.music-list .active-music').classList.remove('active-music');
            }
            event.currentTarget.classList.add('active-music');
            let index = parseInt(event.currentTarget.getAttribute('index'));
            _this.play_name = name;
            //stop the previous sound if any
            this.stopMusic(false);
            // 本地读取，如果没有，则发送请求
            _this.$localforage.getItem('download_' + name).then(function (value) {
                if (value === null) {
                    _this.tips_msg = '开始远程读取文件';
                    _this.$http({
                        method: 'get',
                        url: music_data.download_url,
                        responseType: 'arraybuffer',
                        onDownloadProgress: (evt) => {
                            _this.tips_msg = '下载中... ' + (evt.loaded / evt.total * 100).toFixed() + '%';
                        },
                    }).then(function (response) {
                        let save_name = 'download_' + name;
                        music_data.array_buffer = response.data;
                        _this.$localforage.setItem(save_name, music_data).then(function (value) {
                            _this.$parent.refreshUsage();
                            if (!_this.download_music_list.includes(save_name)) {
                                _this.download_music_list.push(save_name);
                            }
                            _this.loadMusicContent(response.data, index, name);
                        });
                    }).catch(function (error) {
                        console.log(error);
                        _this.tips_msg = '远程文件读取失败';
                    });
                } else {
                    _this.loadMusicContent(value.array_buffer, index, name);
                }
            }).catch(function (err) {
                // 当出错时，此处代码运行
                console.log(err);
            });
        },

        limitNotification: function (headers) {
            let limit = headers['x-ratelimit-limit'];
            let remaining = headers['x-ratelimit-remaining'];
            if (parseInt(remaining) / parseInt(limit) < 0.4) {
                this.$notify({
                    type: 'warning',
                    title: '警告',
                    message: '请求速率限制: ' + remaining + '/' + limit + '每小时',
                    offset: 60
                })
            }
        },

        loadMusicContent: function (data, index, name) {
            let _this = this;
            if (_this.audioContext == null) {
                try {
                    _this.audioContext = new AudioContext();
                } catch (e) {
                    console.log(e);
                    _this.tips_msg = '浏览器不支持AudioContext:(';
                    return
                }
            } else {
                _this.audioContext.close();
                _this.audioContext = null;
                _this.audioContext = new AudioContext();
            }
            let audioContext = _this.audioContext;
            _this.tips_msg = '正在解码...';
            _this.getMusicInfo(data, name);
            audioContext.decodeAudioData(data, function (buffer) {
                //解码成功则调用此函数，参数buffer为解码后得到的结果
                //调用_visualize进行下一步处理
                _this.play_num = index;
                _this._visualize(audioContext, buffer);
            }, function (e) {
                //这个是解码失败会调用的函数
                console.log("文件解码失败:(");
            });
        },

        _visualize: function (audioContext, buffer) {
            let audioBufferSourceNode = audioContext.createBufferSource(),
                analyser = audioContext.createAnalyser(),
                gainNode = audioContext.createGain(),  // 创建一个GainNode,它可以控制音频的总音量
                _this = this;
            gainNode.gain.value = 0;
            _this.duration_time = Math.round(buffer.duration);
            _this.current_time = 0;
            clearInterval(_this.timer);
            _this.start_time = audioContext.currentTime;
            _this.timer = setInterval(function () {
                let time = audioContext.currentTime - _this.start_time;
                _this.setActiveLyrics(time);
                _this.current_time = Math.floor(time);
                _this.percentage = _this.current_time / _this.duration_time * 100;
            }, 100);
            //将source与分析器以及音量进行关联
            audioBufferSourceNode.connect(gainNode);
            audioBufferSourceNode.connect(analyser);
            //将分析器与destination连接，这样才能形成到达扬声器的通路
            // audioContext.destination返回AudioDestinationNode对象，表示当前audioContext中所有节点的最终节点，一般表示音频渲染设备
            gainNode.connect(audioContext.destination);
            //将上一步解码得到的buffer数据赋值给source
            audioBufferSourceNode.buffer = buffer;
            //设置音量
            gainNode.gain.linearRampToValueAtTime(_this.volume / 100, audioContext.currentTime + 1);
            _this.gainNode = gainNode;
            //播放
            if (!audioBufferSourceNode.start) {
                audioBufferSourceNode.start = audioBufferSourceNode.noteOn; //in old browsers use noteOn method
                audioBufferSourceNode.stop = audioBufferSourceNode.noteOff; //in old browsers use noteOff method
            }
            _this.tips_msg = _this.play_name;
            audioBufferSourceNode.start(0);
            document.querySelector('.album-picture').classList.add('play');
            _this.status = 1;
            _this.source = audioBufferSourceNode;
            audioBufferSourceNode.onended = function () {
                _this._audioEnd();
            };
        },

        _audioEnd: function () {
            this.status = 0;
            // 如果是强制停止则不再播放下一首
            if (this.forceStop) {
                this.forceStop = false;
                return
            }
            // 播放结束后自动播放
            if (this.play_model === 0) {
                this.nextMusic();
            } else if (this.play_model === 1) {
                this.playMusic();
            } else if (this.play_model === 2) {
                this.play_num = Math.floor(Math.random() * this.music_list.length);
                this.playMusic();
            }
        },

        preMusic: function () {
            if (this.play_model === 2) {
                this.play_num = Math.floor(Math.random() * this.music_list.length);
            } else {
                this.play_num -= 1;
            }
            if (this.play_num === -1) {
                this.play_num = this.music_list.length - 1;
            }
            if (document.querySelector('div[index="' + this.play_num + '"]')) {
                document.querySelector('div[index="' + this.play_num + '"]').click();
            }
        },

        playMusic: function () {
            if (this.status === 0) {
                if (document.querySelector('div[index="' + this.play_num + '"]')) {
                    document.querySelector('div[index="' + this.play_num + '"]').click();
                }
            } else if (this.status === 1) {
                this.audioContext.suspend();
                this.status = 2;
                document.querySelector('.album-picture').classList.remove('play');
            } else if (this.status === 2) {
                this.audioContext.resume();
                this.status = 1;
                document.querySelector('.album-picture').classList.add('play');
            }
        },

        nextMusic: function () {
            if (this.play_model === 2) {
                this.play_num = Math.floor(Math.random() * this.music_list.length);
            } else {
                this.play_num += 1;
            }
            if (this.play_num === this.music_list.length) {
                this.play_num = 0;
            }
            if (document.querySelector('div[index="' + this.play_num + '"]')) {
                document.querySelector('div[index="' + this.play_num + '"]').click();
            }
        },

        stopMusic: function (value) {
            document.title = 'onedx1943';
            // 是否强制停止，不再播放下一首
            this.forceStop = value;
            clearInterval(this.timer);
            this.duration_time = 0;
            this.current_time = 0;
            this.tips_msg = '';
            this.percentage = 0;
            if (this.animationId !== null) {
                cancelAnimationFrame(this.animationId);
            }
            if (this.source !== null) {
                this.source.stop(0);
            }
            if (this.audioContext != null) {
                this.audioContext.close();
                this.audioContext = null;
            }
            this.status = 0;
            this.imgUrl = 'data:image/jpeg;base64,';
            this.musicTitle = '';
            this.musicAlbum = '';
            this.musicArtist = '';
            this.lyricsContent = [];
            this.musicLyricsMsg = '';
            if (document.querySelector('.album-picture')) {
                document.querySelector('.album-picture').classList.remove('play');
            }
        },

        switchModel: function () {
            this.play_model = (this.play_model + 1) % 3;
        },

        mutedPage: function () {
            this.muted = !this.muted;
            if (this.muted) {
                this.volume_actual = this.volume;
                this.volume = 0;
            } else {
                this.volume = this.volume_actual;
            }
            if (this.gainNode && this.audioContext) {
                this.gainNode.gain.linearRampToValueAtTime(this.volume / 100 * (!this.muted), this.audioContext.currentTime + 1);
            }
        },

        resizeVolume: function (value) {
            this.$localforage.setItem('volume', value);
            this.volume = value;
            this.muted = (this.volume === 0);
            if (this.gainNode && this.audioContext) {
                this.gainNode.gain.linearRampToValueAtTime(this.volume / 100 * (!this.muted), this.audioContext.currentTime + 1);
            }
        },

        getMusicInfo: function (data, name) {
            let _this = this;
            _this.jsMediaTags.read(new Blob([data]), {
                onSuccess: function (tag) {
                    if (tag.tags.title) {
                        _this.musicTitle = tag.tags.title;
                        _this.getLyricsContent(tag.tags.title);
                    } else {
                        let show_name = name.substring(0, name.lastIndexOf("."))
                        _this.musicTitle = show_name;
                        _this.getLyricsContent(show_name);
                    }
                    document.title = _this.musicTitle;
                    if (tag.tags.album) {
                        _this.musicAlbum = '专辑：' + tag.tags.album;
                    } else {
                        _this.musicAlbum = '专辑：未知';
                    }
                    if (tag.tags.artist) {
                        _this.musicArtist = '歌手：' + tag.tags.artist;
                    } else {
                        _this.musicArtist = '歌手：未知';
                    }
                    let picture = tag.tags.picture;
                    if (picture) {
                        let imageReader = new FileReader();
                        imageReader.onload = function (e) {
                            _this.imgUrl = e.target.result;
                            _this.$localforage.setItem('img_' + name, _this.imgUrl).then(function () {
                                _this.$parent.refreshUsage();
                            });
                            if (_this.playlist_img === 'data:image/jpeg;base64,') {
                                _this.playlist_img = _this.imgUrl
                            }
                        };
                        imageReader.readAsDataURL(new Blob([new Uint8Array(picture.data)], { type: picture.format }));
                    } else {
                        _this.imgUrl = 'data:image/jpeg;base64,';
                    }
                },
                onError: function (error) {
                    console.log(error);
                    _this.imgUrl = 'data:image/jpeg;base64,';
                }
            });
        },

        getMusicLyrics: function (file_api) {
            let _this = this;
            _this.$http.get(file_api, {
                headers: {
                    'Authorization': _this.$token1 + _this.$token2 + _this.$token3
                }
            }).then(function (response) {
                _this.limitNotification(response.headers);
                for (let i = 0; i < response.data.length; i++) {
                    if (response.data[i].name.endsWith('.lrc')) {
                        _this.lyricsList.push(response.data[i])
                    } else if (response.data[i].type === 'dir') {
                        let new_url = file_api + '/' + response.data[i].name;
                        _this.getMusicLyrics(new_url);
                    }
                }
            }).catch(function (error) {
                console.log(error);
            });
        },

        getLyricsContent: function (name) {
            let _this = this;
            _this.$localforage.getItem('lyrics_' + name).then(function (value) {
                if (value === null) {
                    let url = null;
                    for (let lyrics of _this.lyricsList) {
                        if (lyrics.name.includes(name)) {
                            url = lyrics.download_url;
                            break
                        }
                    }
                    if (url) {
                        _this.$http.get(url).then(function (response) {
                            _this.$localforage.setItem('lyrics_' + name, response.data).then(function (value) {
                                _this.$parent.refreshUsage();
                                _this.loadLyricsContent(response.data);
                            });
                        }).catch(function (error) {
                            console.log(error);
                        });
                    } else {
                        _this.musicLyricsMsg = '暂无歌词';
                    }
                } else {
                    _this.loadLyricsContent(value);
                }
            }).catch(function (err) {
                console.log(err);
            });
        },

        loadLyricsContent: function (data) {
            let items = data.split(/[\r\n]/);
            for (let item of items) {
                let time = item.substring(item.indexOf("[") + 1, item.indexOf("]"));
                if ((/\d+:\d+.\d+/).test(time)) {
                    this.lyricsContent.push({
                        time: (time.split(":")[0] * 60 + parseFloat(time.split(":")[1])).toFixed(3),
                        content: item.substring(item.indexOf("]") + 1, item.length)
                    })
                }
            }
            this.lyricsContent.unshift({ time: 0, content: '' });
            if (this.lyricsContent.length === 1) {
                this.musicLyricsMsg = '暂无歌词';
            }
        },

        setActiveLyrics: function (time) {
            let _this = this;
            for (let num = 0, len = _this.lyricsContent.length; num < len; num++) {
                if (num === len - 1 || (_this.lyricsContent[num].time <= time && time < _this.lyricsContent[num + 1].time)) {
                    let activeLyricsDom = document.querySelector('.music-lyrics div[time="' + _this.lyricsContent[num].time + '"]');
                    if (!activeLyricsDom.classList.contains('active')) {
                        let scroll = activeLyricsDom.offsetTop - 160;
                        if (scroll > 0) {
                            document.querySelector('.music-lyrics').scrollTo({
                                top: scroll,
                                behavior: 'smooth'
                            });
                        }
                        if (document.querySelector('.music-lyrics div.active')) {
                            document.querySelector('.music-lyrics div.active').classList.remove('active');
                        }
                        activeLyricsDom.classList.add('active');
                    }
                    break
                }
            }
        },
    },
}
</script>

