<template>
    <el-row class="main-content">
        <el-col>
            <div class="switch-icon-prefix">
                <el-switch v-model="icon_prefix"
                           active-color="#409EFF"
                           inactive-color="#67C23A"
                           active-value="fas"
                           inactive-value="fab">
                </el-switch>
            </div>
            <div class="icons-list"
                 v-if="icon_list.length > 0">
                <div class="icon-content"
                     v-for="icon in icon_list"
                     :key="icon"
                     @click="copyIconName(icon)">
                    <i :class="`${icon_prefix} ${icon}`"></i>
                    <span>{{ icon }}</span>
                </div>
            </div>
            <div class="icons-msg"
                 v-else>
                {{ icon_msg }}
            </div>
        </el-col>
    </el-row>
</template>

<script>
export default {
    name: 'AppIcon',
    data: function () {
        return {
            icon_list: [],
            icon_msg: '',
            icon_prefix: 'fas' // fab
        }
    },

    created: function () {
        // 获取所有的css文件并筛选出font awesome对应的文件
        let css_files = document.styleSheets;
        let icon_list = [];
        for (let css_file of css_files) {
            for (let rule of css_file.cssRules) {
                if (rule.selectorText && rule.selectorText.startsWith('.fa-') && rule.selectorText.endsWith('::before')) {
                    icon_list.push(rule.selectorText.slice(1, rule.selectorText.indexOf('::')));
                }
            }
        }
        this.icon_list = icon_list;
        if (this.icon_list.length === 0) {
            this.icon_msg = '似乎没找到css文件 (ノへ￣、)';
        }
    },

    methods: {
        copyIconName: function (name) {
            let copy_text = `${this.icon_prefix} ${name}`;
            let input = document.createElement("input");
            input.setAttribute('display', 'none');
            document.body.appendChild(input);
            input.setAttribute('value', copy_text);
            input.select();
            if (document.execCommand('copy')) {
                document.execCommand('copy');
            }
            document.body.removeChild(input);
            this.$notify({
                type: 'success',
                title: '提示',
                message: 'Icon已复制: ' + copy_text,
                offset: 60
            })
        }
    },
}
</script>

<style scoped>
.icons-list {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: flex-start;
    padding: 40px;
    margin-left: -20px;
}

.icon-content {
    width: 150px;
    height: 120px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: xx-large;
    flex-direction: column;
    cursor: pointer;
    border-radius: 12px;
    transition: 0.3s;
    user-select: none;
    margin-bottom: 20px;
    margin-left: 20px;
    padding: 0 5px;
    text-align: center;
}

.icon-content:hover {
    background-image: linear-gradient(120deg, #dcdfe6, #f2f6fc);
}

.icon-content > i {
    margin: 0 20px 20px;
    font-size: 30px;
}
.icon-content > span {
    display: inline-block;
    width: 100%;
    font-size: 14px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}
.switch-icon-prefix {
    position: fixed;
    top: 80px;
    right: 40px;
}
</style>



