<template>
    <div class="home-content">
        <div class="btn-container">
            <router-link to="/app"
                         class="btn">Go to App</router-link>
            <router-link to="/blog"
                         style="display: none"
                         class="btn">Go to Blog</router-link>
        </div>
    </div>
</template>

<script>
export default {
    name: "Home",
    data () {
        return {};
    },
};
</script>

<style scoped>
.home-content {
    color: #fff;
    text-align: center;
    background-color: #159957;
    background-image: linear-gradient(120deg, #155799, #159957);
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.btn-container {
    width: 500px;
    display: flex;
    justify-content: space-around;
}
.btn {
    padding: 0.5rem 1rem;
    font-size: 16px;
    cursor: pointer;
    user-select: none;
    text-decoration: none;
    display: inline-block;
    color: #ffffffb3;
    background-color: #ffffff14;
    border: 1px solid #ffffff33;
    border-radius: 4px;
    transition: color 0.2s, background-color 0.2s, border-color 0.2s;
}
.btn:hover {
    color: #ffffffcc;
    background-color: #ffffff33;
    border-color: #ffffff4d;
}
</style>



