<template>
    <div class="main-content">
        <el-row>
            <el-col>
                <div class="video-header">
                    <div class="video-custom">
                        <el-input placeholder="请输入内容"
                                  v-model="custom_api">
                            <template slot="prepend">从自定义github仓获取视频</template>
                            <el-button slot="append"
                                       icon="el-icon-search"
                                       @click="loadCustomApi"></el-button>
                        </el-input>
                    </div>
                </div>
            </el-col>
        </el-row>
        <div class="video-content">
            <div id="mui-player"></div>
        </div>
        <el-input prefix-icon="el-icon-search"
                  placeholder="仅供搜索，别想太多"
                  v-model="filterText"
                  clearable>
        </el-input>
        <div class="video-list mini-scroll"
             v-if="video_list.length > 0">
            <div v-for="(video, index) in video_list"
                 :index="index"
                 :key="video.sha"
                 @click="getVideoContent($event, video)">{{ index + 1 }}. {{ video.name }} ( {{ (video.size /
        1024 / 1024).toFixed(2) + 'M' }} )
            </div>
        </div>
        <div v-else>{{ video_msg }}</div>
    </div>
</template>

<script>
import 'mui-player/dist/mui-player.min.css'
import MuiPlayer from 'mui-player'
import MuiPlayerDesktopPlugin from 'mui-player-desktop-plugin'

export default {
    name: 'AppVideo',
    filters: {},

    data: function () {
        return {
            activeNames: ['1'],
            video_api: 'https://api.github.com/repos/onedx1943/Music/contents',
            custom_api: 'https://api.github.com/repos/onedx1943/Music/contents',
            video_list: [],
            video_msg: '正在读取文件...',
            filterText: '',
            videoPlayer: null,
        }
    },

    watch: {
        filterText: function (val) {
            this.filter_video(val.toLowerCase())
        },
    },

    computed: {},

    mounted: function () {
        this.getVideoList(this.video_api);
    },

    beforeDestroy: function () {
        // 组件销毁前结束播放
        this.stopVideo();
    },

    methods: {
        getVideoList: function (file_api) {
            let _this = this;
            _this.video_msg = '正在查找文件！';
            _this.$http.get(file_api, {
                headers: {
                    'Authorization': _this.$token1 + _this.$token2 + _this.$token3
                }
            }).then(function (response) {
                _this.limitNotification(response.headers);
                for (let i = 0; i < response.data.length; i++) {
                    if (response.data[i].name.endsWith('.mp4')) {
                        _this.video_list.push(response.data[i])
                    } else if (response.data[i].type === 'dir') {
                        let new_url = file_api + '/' + response.data[i].name;
                        _this.getVideoList(new_url);
                    }
                }
            }).catch(function (error) {
                console.log(error);
                _this.video_msg = '网络或者链接有问题啊，读取失败了！';
            });
        },

        getVideoContent: function (event, video) {
            let _this = this;
            if (document.querySelector('.video-list .active-video')) {
                document.querySelector('.video-list .active-video').classList.remove('active-video')
            }
            event.currentTarget.classList.add('active-video')
            let index = parseInt(event.currentTarget.getAttribute('index'));
            this.videoPlayer = new MuiPlayer({
                container: '#mui-player',
                title: video.name,
                src: video.download_url,
                themeColor: '#409EFF', // 自定义主题颜色
                plugins: [
                    new MuiPlayerDesktopPlugin()
                ]
            })
        },

        limitNotification: function (headers) {
            let limit = headers['x-ratelimit-limit'];
            let remaining = headers['x-ratelimit-remaining'];
            if (parseInt(remaining) / parseInt(limit) < 0.4) {
                this.$notify({
                    type: 'warning',
                    title: '警告',
                    message: '请求速率限制: ' + remaining + '/' + limit + '每小时',
                    offset: 60
                })
            }
        },

        stopVideo: function () {
            if (this.videoPlayer) {
                this.videoPlayer.destory();
            }
        },

        loadCustomApi: function () {
            // 检测链接合法性
            let reg = new RegExp(this.$apiReg, 'i');
            this.video_list = [];
            this.filterText = '';
            if (reg.test(this.custom_api)) {
                this.getVideoList(this.custom_api);
            } else {
                this.video_msg = '链接格式：https://api.github.com/repos/用户名/仓库名/contents（仓库内具体路径可以接着加 /xxx/xxx）';
            }
        },

        filter_video: function (val) {
            document.querySelectorAll('.video-list div').forEach(item => {
                if (item.innerText.toLowerCase().includes(val)) {
                    item.style.display = 'block'
                } else {
                    item.style.display = 'none'
                }
            })
        }
    },
}
</script>

<style scoped>
.video-content {
    margin: 10px 0;
}
.video-list {
    min-height: 300px;
    max-height: 450px;
    overflow-y: auto;
    margin-top: 12px;
}
.video-list > div {
    cursor: pointer;
    white-space: nowrap;
    overflow-x: hidden;
    box-shadow: 0 0 1px rgba(0, 0, 0, 0.2);
    padding: 0 20px;
    height: 32px;
    line-height: 32px;
    font-size: 18px;
}
.video-list > div:hover {
    color: cornflowerblue;
    background-color: aliceblue;
}
.video-list .active-video {
    color: #2ea44f;
}
</style>



